<script>
// const mapboxgl = require('mapbox-gl');
import mapboxgl from 'mapbox-gl';
import MapboxDirections from '@mapbox/mapbox-gl-directions/dist/mapbox-gl-directions';
import { MapboxStyleSwitcherControl } from 'mapbox-gl-style-switcher/dist/index';
import 'mapbox-gl-style-switcher/styles.css';
const togpx = require('togpx');
const MD = MapboxDirections;
// import { MapboxStyleDefinition, MapboxStyleSwitcherControl } from "mapbox-gl-style-switcher";
export default {
    name: 'app',
    components: {},
    mounted() {
        initMap();
    },
};

function saveFile(filename, data) {
    //https://stackoverflow.com/questions/3665115/create-a-file-in-memory-for-user-to-download-not-through-server
    var blob = new Blob([data], { type: 'text/csv' });
    if (window.navigator.msSaveOrOpenBlob) {
        window.navigator.msSaveBlob(blob, filename);
    } else {
        var elem = window.document.createElement('a');
        elem.href = window.URL.createObjectURL(blob);
        elem.download = filename;
        document.body.appendChild(elem);
        elem.click();
        document.body.removeChild(elem);
    }
}

function compileFunc(e) {
    console.log(e);
    return 'HAHA';
}

window.downloadGpx = function() {
    const routeGeo = {
        type: 'FeatureCollection',
        features: map
            .getSource('directions')
            ._data.features.filter(f => f.geometry.type === 'LineString')
            .map(f => ((f.type = 'Feature'), f)), // missing, see https://github.com/mapbox/mapbox-gl-directions/issues/205
    };
    const gpx = togpx(routeGeo);
    saveFile('route.gpx', gpx);
};

function showGpxDownloadLink(e) {
    console.log(e);
    console.log(map.getSource('directions')._data);

    document.getElementsByClassName(
        'mapbox-directions-instructions',
    )[0].innerHTML = `
    <a href="#" onClick="downloadGpx()">Download route as GPX</a>.
    `;
}

function initMap() {
    mapboxgl.accessToken =
        'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJGcW03aExzIn0.QUkUmTGIO3gGt83HiRIjQw';
    var map = new mapboxgl.Map({
        container: 'map',
        // style: 'mapbox://styles/stevage/cjopcl1j9555x2rt298enfwhf',
        style: 'mapbox://styles/stevage/cjqvz0wgj08k32sukjlngs77n',
        center: [145, -37.5],
        zoom: 8,
        // center: [144.47665, -36.84796],
        // zoom: 8,
    });
    window.map = map;
    const directionsControl = new MapboxDirections({
        accessToken: mapboxgl.accessToken,
        unit: 'metric',
        profile: 'mapbox/cycling',
        compile: compileFunc,
        styles: require('./directionsStyle'),
        flyTo: false,
    });
    directionsControl.on('route', showGpxDownloadLink);
    map.addControl(directionsControl, 'top-left');
    // window.dl = require('@mapbox-gl-directions/dist/
    let directionsLayers = [],
        directionsSource = {};
    map.on('styledata', e => {
        console.log(e);
        const curStyle = map.getStyle();
        if (directionsLayers.length === 0 && curStyle.sources['directions']) {
            directionsLayers = curStyle.layers.filter(
                l => l.source === 'directions',
            );
            directionsSource = curStyle.sources['directions'];
            window.directionsLayers = directionsLayers;
        } else if (directionsLayers.length > 0) {
            const newStyle = e.style.stylesheet; //map.getStyle();
            if (!map.getSource('directions')) {
                if (!map.getSource('directions')) {
                    // TODO: somehow preserve the old contents of this layer, so we don't lose directions when switching layer.
                    map.addSource('directions', directionsSource);
                }
                directionsLayers.forEach(l => {
                    if (!map.getLayer(l)) {
                        map.addLayer(l);
                    }
                });
            }
        }
    });
    const localStyles = require('./styles').default;
    console.log(localStyles);
    const styles = [
        {
            //   title: 'New cycletour',
            //   uri: 'mapbox://styles/stevage/cjqk6ve931h5o2spctaxzuu4h'
            // }, {
            title: 'New cycletour',
            uri: 'mapbox://styles/stevage/cjqvz0wgj08k32sukjlngs77n',
        },
        {
            title: 'Outdoors',
            uri: 'mapbox://styles/stevage/cjopcl1j9555x2rt298enfwhf',
        },
        {
            title: 'Light',
            uri: 'mapbox://styles/mapbox/light-v9',
        },
        {
            title: 'Old cycletour',
            uri: localStyles.oldCycletour,
        },
        {
            title: 'VicMap',
            uri: localStyles.vicmap,
        },
    ];
    console.log({ styles });

    map.addControl(new MapboxStyleSwitcherControl(styles));
    window.map = map;
}

/*
